import React, { useState, useEffect } from 'react'

import Blogs from './components/Blogs'
import LoginForm from './components/LoginForm'
import Message from './components/Message'

import blogService from './services/blogs'
import loginService from './services/login'

import './App.css'

const App = () => {
  const [blogs, setBlogs] = useState([])
  const [username, setUsername] = useState(null)
  const [newUser, setNewUser] = useState('')
  const [password, setPassword] = useState('')
  const [errorMessage, setErrorMessage] = useState({ message: null, error: false })

  useEffect(() => {
    blogService.getAll().then(blogs =>
      setBlogs( blogs )
    )
  }, [])

  useEffect(() => {
    console.log('logged in ?')
    const loggedUserJSON = window.localStorage.getItem('loggedInBlogUser')
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON)
      setUsername(user)
      blogService.setToken(user.token)
    }
  }, [])

  const handleChangeUser = (event) => {
    setNewUser(event.target.value)
  }

  const handleChangePassword = (event) => {
    setPassword(event.target.value)
  }

  const handleLogin = async (event) => {
    event.preventDefault()

    try {
      const user = await loginService.login({ username: newUser, password })
      window.localStorage.setItem('loggedInBlogUser', JSON.stringify(user))

      await blogService.setToken(user.token)
      setUsername(user)
      setNewUser('')
      setPassword('')
    } catch (err) {

      setErrorMessage({ message: 'Wrong credentials', status: true })

      setTimeout(() => {
        setErrorMessage({ message: null, error: false })
      }, 3000)
    }
  }

  const handleLogout = async (event) => {
    event.preventDefault()

    window.localStorage.clear()
  }

  return (
    <div>
      <Message message={errorMessage} />
      {username !== null ?
        <div>
          <Blogs blogs={blogs} user={username}/>
          <button onClick={handleLogout}>LOGOUT</button>
        </div>:
        <LoginForm
          handleLogin={handleLogin}
          handleChangeUser={handleChangeUser}
          handleChangePassword={handleChangePassword}
          newUser={newUser}
          password={password}
        />
      }
    </div>
  )
}

export default App